/* Header.css */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* background-color: #007bff; */
    color: #000000;
    padding: 10px 20px;
  }
  
  .header h1 {
    margin: 0;
  }
  
  .cart-icon {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
  }

  .cart-icon:hover {
    color: #ccc;
  }
  
  .cart-counter {
    background-color: red;
    color: white;
    border-radius: 50%;
    font-size: 0.8rem;
    padding: 2px 6px;
    position: absolute;
    top: -10px;
    right: -10px;
  }
  
  .header {
    background-color: rgba(75, 192, 192, 1);
    padding: 10px 0;
    text-align: center;
    position: sticky;
    top: 0;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .remaining-budget {
    margin-right: 20px;
  }
  